import {environment} from 'src/environments/environment';
import {Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {NotificationResponse} from 'src/app/core/models/common/notification/response/notification-response.viewmodel';

@Component({
	selector: 'app-notification',
	templateUrl: './notification.component.html',
	styleUrls: ['./notification.component.less']
})
export class NotificationComponent implements OnInit {
	@Input() fontSize: number=24;
	@Input() notificationCount: number;
	@Input() notificationList: Array<NotificationResponse>=[];
	@Input() isLoading: boolean=true;
	@Output() isNotificationListOpen = new EventEmitter<boolean>();
	@Output() startOfIndex = new EventEmitter<number>();
	isNotificationVisible: boolean = false;
	startIndexScroll: number = 1;
	host: string = environment.apiUrl;

	constructor() {
		this.notificationCount = 0;
	}
	ngOnInit(): void {}
	onScroll() {
		this.startOfIndex.emit((this.startIndexScroll = this.startIndexScroll + 1));
	}
	changeShowNotificationListStatus(value: boolean): void {
		this.startIndexScroll = 1;
		this.startOfIndex.emit(this.startIndexScroll);
		this.isNotificationListOpen.emit(value);
	}
}
